html {
  font-size: 12px;
  line-height: 0; }

body {
  font-family: "Roboto-Regular", sans-serif;
  font-weight: normal;
  font-style: normal; }

[class^="amg-"] {
  box-sizing: border-box; }

.amg-outdate-modal-opened {
  height: 100% !important; }

.amg-outdate-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 700px;
  background-color: #808080;
  z-index: 10000;
  display: none; }

.amg-outdate {
  width: 960px;
  height: 600px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  background-image: url("../img/amg-outdate-bg-car.png");
  background-repeat: no-repeat;
  background-size: 960px 600px;
  padding-top: 56px;
  padding-left: 85px;
  padding-right: 95px;
  padding-bottom: 50px; }
  .amg-outdate__title {
    font-family: "Roboto-Regular", sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 40px;
    text-transform: uppercase;
    line-height: 54px;
    letter-spacing: 0.02em;
    color: #404041;
    margin-top: 0;
    margin-bottom: 45px; }
  .amg-outdate__title-more {
    color: #e62d2a; }
  .amg-outdate__text {
    font-family: "Roboto-Thin", sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 13px;
    line-height: 17px;
    letter-spacing: 0.03em;
    margin-top: 0; }
    .amg-outdate__text:last-child {
      margin-bottom: 0; }
  .amg-outdate__close {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 23px;
    height: 23px;
    background-color: transparent;
    border: 0;
    appearance: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
    line-height: 1;
    outline: none;
    background-image: url("../img/amg-outdate-cross.png");
    background-repeat: no-repeat;
    background-size: 23px 23px; }
    .amg-outdate__close .amg-cross {
      transition: all 0.5s ease;
      fill: #e62d2a; }
    .amg-outdate__close:hover .amg-cross {
      fill: #000; }
    .amg-outdate__close:hover {
      background-image: url("../img/amg-outdate-cross--hover.png"); }
  .amg-outdate__browsers-stack {
    display: table;
    width: 100%;
    position: relative; }
  .amg-outdate__browser {
    transition: all 0.5s ease;
    width: 124px;
    height: 144px;
    display: table-cell;
    padding-top: 20px;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 20px;
    text-decoration: none;
    font-size: 0;
    text-align: center;
    vertical-align: middle; }
    .amg-outdate__browser:hover {
      box-shadow: 0 0 20px #cfcfcf; }
    .amg-outdate__browser--isUsed {
      width: 168px;
      height: 188px;
      position: relative; }
  .amg-outdate__browser-logo {
    width: 100%;
    object-fit: contain; }
  .amg-outdate__browser-name {
    font-family: "Roboto-Light", sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #373839;
    font-size: 13px;
    line-height: 18px;
    margin-top: 10px; }
  .amg-outdate__btn-thx {
    font-family: "Roboto-Bold", sans-serif;
    font-weight: normal;
    font-style: normal;
    border: 0;
    outline: none;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    background-color: transparent;
    color: #e62d2a;
    margin: 0 auto;
    margin-top: 17px;
    display: block;
    position: relative; }
    .amg-outdate__btn-thx:hover {
      transition: all 0.5s ease;
      color: #414041; }

body {
  height: 2000px; }
